/* Chat Container */
.chat-container {
    width: 400px;
    margin: auto;
    text-align: center;
    font-family: Arial, sans-serif;
  }
  
  /* Join Screen */
  .join-screen {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 10px;
    padding: 20px;
  }
  
  .join-screen input {
    padding: 8px;
    width: 80%;
  }
  
  .join-screen button {
    padding: 10px 20px;
    background-color: #007bff;
    color: white;
    border: none;
    cursor: pointer;
  }
  
  /* Chat Box */
  .chat-box {
    display: flex;
    flex-direction: column;
    border: 1px solid #ddd;
    border-radius: 5px;
    overflow: hidden;
  }
  
  /* Messages */
  .messages {
    height: 300px;
    overflow-y: auto;
    padding: 10px;
    background-color: #f9f9f9;
  }
  
  .message {
    display: flex;
    flex-direction: column;
    padding: 5px 10px;
    margin: 5px;
    border-radius: 10px;
    max-width: 75%;
  }
  
  .own {
    background-color: #14ff18;
    align-self: flex-end;
    float:right;
   
    width:80%;
  }
  
  .other {
    background-color: #ffffff;
    align-self: flex-start;
    border: 1px solid #ddd;
    float:left;
   width:80%;
  }
  
  .username {
    font-size: 12px;
    font-weight: bold;
    color: #555;
  }
  
  .input-area {
    display: flex;
    border-top: 1px solid #ddd;
    padding: 10px;
  }
  
  .input-area input {
    flex: 1;
    padding: 8px;
    border: 1px solid #ddd;
    border-radius: 5px;
  }
  
  .input-area button {
    padding: 8px 15px;
    background-color: #007bff;
    color: white;
    border: none;
    cursor: pointer;
    margin-left: 5px;
  }
  

  @media  screen and (max-width:600px) {
 body{
    background-color: rgb(25, 25, 25);
 }
 h2{
    color:white;
 }
    html{
        font-size: 1.1rem;
    }
    button{
        margin-top: 0.5rem;
        font-size: 1.1rem;
    }
    input{
        font-size: 1.1rem;
    }
    input:focus{
        border:2px solid green;
        outline:none;
    }
    .chat-container {
       background-color: black;
        border: 1px solid #ddd;
        border-radius: 5px;
        overflow: hidden;
        width:100%;
        height:100%;
      }
      .messages{
        height: 100vh;
        background-color: black;
      }
      .input-area button {
        padding: 8px 15px;
        background-color: #007bff;
