        border: none;
        cursor: pointer;
        margin-left: 5px;
        font-size: 1.3rem;
        padding:0.5rem 0.7rem;
        border-radius: 0.5rem;
      }
      .own p{
        font-size: 25px;
        
      }
      .own span{
